.playerContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  max-height: 48px;
  background-color: #333333;
  padding: 5px;
}

.heroInfo {
  display: flex;
  width: 40%;
  gap: 10px;
}
.heroInfo_symmetric {
  display: flex;
  justify-content: flex-end;
}

.heroInfo__image {
  display: block;
  width: 48px;
}
.heroInfo__image:hover {
  outline: 1px solid #f3a605;
  cursor: pointer;
}

.heroInfo__link:hover {
  color: #f3a605;
}

.kdaInfo {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 5px;
  width: 30%;
}

.kdaInfo__kills {
  color: #72bc9b;
}

.kdaInfo__deaths {
  color: #e9422e;
}

.kdaInfo__assists {
  color: #fac552;
}

.itemsInfo {
  display: grid;
  width: fit-content;
  grid-template-columns: repeat(4, 24px);
}

.itemsInfo__image {
  display: block;
  width: 24px;
}
