.page {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 30px 0;
}

.hero {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.hero__info {
  display: flex;
  flex-direction: column;
  gap: 20px;
  max-width: 300px;
}

.hero__image {
  display: block;
  max-width: 120px;
  margin: 0 auto;
}

.hero__title {
  display: block;
  margin: 0 auto;
}

.hero__tags {
  display: flex;
  justify-content: center;
  gap: 10px;
  list-style-type: none;
  margin: 0 auto 0 0;
  padding: 20px 0 0 0;
  width: 100%;
  border-top: 1px solid #f3a805be;
}

.hero__roles {
  display: flex;
  justify-content: center;
  gap: 10px;
  list-style-type: none;
  padding: 20px 0 0 0;
  margin: 0;
  width: 100%;
  border-top: 1px solid #f3a805be;
}

.role__image {
  display: block;
}

.hero__stats {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  list-style-type: none;
  gap: 8px;
  margin: 0 auto 0 0;
  padding: 0;
  width: 100%;
  border-top: 1px solid #f3a805be;
  padding-top: 20px;
}

.hero__stat {
  display: grid;
  align-items: center;
  grid-template-columns: auto 7% 50%;
  grid-gap: 8px;
}

.stat__label {
  width: 30%;
}

.stat__value {
  text-align: center;
  color: #f3a605;
}

.stat__pgbar {
  min-height: 9px;
  background-color: #f3a605;
}

.rate {
  display: flex;
  gap: 10%;
}

.rate__label {
  min-width: 25%;
  margin: auto 0;
}

.pgbar {
  display: flex;
  flex-direction: row;
  align-items: center;
  border: 1px solid #eeeeee;
  width: 100%;
  position: relative;
}

.pgbar__filled {
  min-height: 24px;
  background-color: rgba(155, 155, 155, 0.4);
}

.pgbar__label {
  position: absolute;
  display: flex;
  justify-content: center;
  width: 100%;
}

.hero__abilities {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
  gap: 20px;
  list-style: none;
  padding: 0;
  margin: 0;
}

.ability {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 2px 14px 8px;
  width: 30%;
  outline: 1px solid #eeeeee50;
}

.ability__header {
  display: flex;
  flex-direction: row;
  gap: 10px;
}

.ability__title {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0;
}

.ability__name {
  font-size: 20px;
  padding: 10px 0;
  margin: 0;
}

.ability__key {
  font-size: 18px;
  padding: 10px 0;
  margin: 0;
  color: #ffe495;
}

.ability__image {
  display: block;
  max-width: 20%;
}

.ability__footer {
  display: flex;
  flex-direction: column;
  color: #ffe495;
  margin-top: auto;
}
