.header {
  background-color: #333333;
  border-bottom: 3px solid #f3a605;
}

.nav {
  display: flex;
  justify-content: space-between;
  margin: 0 10%;
}

.list {
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  list-style-type: none;
}

.link {
  padding: 0 24px;
  display: flex;
  align-items: center;
  color: #a6a6a6;
  border-bottom: 3px solid transparent;
}

.link:hover {
  color: #ffffff;
  border-bottom: 3px solid #c0853f;
}

.link_active {
  color: #ffffff;
  border-bottom: 3px solid #ffe495;
}

.link_active:hover {
  cursor: default;
  border-bottom: 3px solid #ffe495;
}

.link__icon {
  width: 22px;
  margin: 0 10px 0 0;
}

.link__text {
  margin: 15px 0;
  font-size: 18px;
  font-weight: 400;
  letter-spacing: 2px;
  text-transform: uppercase;
}

.searchbox {
  padding: 0 20px;
  margin: 10px 0;
  border: 2px solid #767676;
  background: transparent;
}

.searchbox:focus {
  border: 2px solid #eaeae9;
  outline: none;
}
