.page {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 20px 0;
  gap: 20px;
}

.player {
  min-width: 300px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.player__info {
  display: flex;
  flex-direction: column;
  gap: 10px;
  background-color: #333333;
  padding: 6px;
}

.player__rank {
  position: relative;
}

.rank__image {
  width: 100%;
}

.rank__text {
  position: absolute;
  top: 30%;
  left: 33%;
  display: flex;
  flex-direction: column;
  gap: 4px;
  text-align: center;
  color: #e8c701;
  z-index: 99;
}

.rank__vp {
  font-size: 52px;
}

.rank__title {
  border-top: 1px solid #e8c701;
  width: fit-content;
}

.player__stats {
  display: flex;
  flex-direction: column;
  gap: 10px;
  background-color: #333333;
  padding: 6px;
}

.stroke {
  display: flex;
  justify-content: space-between;
}

.matches {
  width: 100%;
}

.matchList {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.match {
  display: flex;
}

.match__info {
  min-width: 75px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.hero {
  margin-right: auto;
  position: relative;
  width: 72px;
  height: 72px;
}

.role {
  position: absolute;
  top: 100%;
}

.defeat {
  color: #f54530;
}

.victory {
  color: #3ad289;
}

.match__items {
  margin: 0 auto;
}

.items__list {
  display: flex;
  align-items: center;
  gap: 8px;
}

.item__image {
  outline: 1px solid #46578a;
  width: 60px;
  height: 60px;
}

.item__image:hover {
  outline: 1px solid #f2c802;
  cursor: pointer;
}

.items__list .item__image:first-of-type {
  width: 72px;
  height: 72px;
  margin-right: 8px;
}

.items__list .item__image:last-of-type {
  width: 50px;
  height: 50px;
}

.items__list .item__image:nth-last-of-type(2) {
  width: 50px;
  height: 50px;
  margin-left: 8px;
}

.match__footer {
  min-width: 135px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-left: auto;
  font-size: 18px;
}

.match__cg {
  margin-left: auto;
  display: flex;
  align-items: center;
}

.cg__image {
  width: 22px;
}

.match__duration {
  margin-left: auto;
}

.match__date {
  margin-left: auto;
}
