.page {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 20px 0;
  gap: 20px;
}

/* Left panel */
.filters {
  min-width: 230px;
}

.filters__header {
  margin: 10px 0 6px;
  text-transform: uppercase;
  font-weight: 400;
  font-size: 20px;
  letter-spacing: 1px;
  line-height: 36px;
  border-bottom: 1px solid #ffffff;
  border-image: linear-gradient(to right, #ffffff, 50%, transparent, transparent);
  -moz-border-image: -moz-linear-gradient(to right, #ffffff, 50%, transparent, transparent);
  -webkit-border-image: -webkit-linear-gradient(to right, #ffffff, 50%, transparent, transparent);
  border-image-slice: 1;
  color: #e5e5e5;
}

.filters__header:first-child {
  margin: 0 0 6px;
}

.filters__list {
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  font-size: 20px;
  line-height: 24px;
  gap: 6px;
}

.filter__icon {
  width: 28px;
}

.filter {
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  gap: 6px;
  text-transform: uppercase;
  font-size: 16px;
  line-height: 22px;
  font-weight: 200;
  letter-spacing: 1px;
  border: none;
  border-left: 2px solid transparent;
  background-color: transparent;
  color: #e5e5e5;
}

.filter:hover {
  cursor: pointer;
  border-left: 2px solid #ffb45a;
  background: linear-gradient(to right, #6a5a45, 10%, transparent);
}

.filter_active {
  border-left: 2px solid #ffb45a;
  background: linear-gradient(to right, #6a5a45, 50%, transparent);
}

/* upper roles panel */
.classes {
  display: flex;
  gap: 20px;
  padding: 0 16px 16px;
}

.class__btn {
  mask-image: url('../../images/roles/role-all.png');
  -webkit-mask-image: url('../../images/roles/role-all.png');
  mask-size: contain;
  -webkit-mask-size: contain;
  mask-repeat: no-repeat;
  -webkit-mask-repeat: no-repeat;
  mask-position: center;
  -webkit-mask-position: center;
  border: none;
  width: 40px;
  height: 40px;
  opacity: .5;
}

.class__btn:hover {
  cursor: pointer;
  opacity: 1;
}

.class__btn_active {
  background-color: #f8e8a6;
  opacity: 1;
}

.class__btn_active:hover {
  cursor: default;
}

.class__btn_assassin {
  mask-image: url('../../images/classes/assassin.png');
  -webkit-mask-image: url('../../images/classes/assassin.png');
}

.class__btn_fighter {
  mask-image: url('../../images/classes/fighter.png');
  -webkit-mask-image: url('../../images/classes/fighter.png');
}

.class__btn_tank {
  mask-image: url('../../images/classes/tank.png');
  -webkit-mask-image: url('../../images/classes/tank.png');
}

.class__btn_mage {
  mask-image: url('../../images/classes/mage.png');
  -webkit-mask-image: url('../../images/classes/mage.png');
}

.class__btn_sharpshooter {
  mask-image: url('../../images/classes/sharpshooter.png');
  -webkit-mask-image: url('../../images/classes/sharpshooter.png');
}

.class__btn_support {
  mask-image: url('../../images/classes/support.png');
  -webkit-mask-image: url('../../images/classes/support.png');
}

/* Items section */
.items {
  width: 100%;
}

.item_mapping {
  list-style-type: none;
  padding: 8px;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 22px 18px;
  overflow-y: scroll;
  max-height: 700px;
}

.item_mapping::-webkit-scrollbar {
  width: 8px;
  background: #333333;
}

.item_mapping::-webkit-scrollbar-thumb {
  background: #ce9a1b;
}

.item {
  width: 90px;
  max-height: 124px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0px;
}

.item__image {
  display: block;
  max-width: 100%;
  outline: 1px solid #f5b35d;
}

.item__price {
  margin: 8px auto;
  color: #eeeff3;
  font-size: large;
}

.item__link:hover > .item > .item__image {
  outline: 3px solid #ffe495;
}

.item__link:hover > .item > .item__price {
  color: #ffe495;
}

.item_disabled {
  display: none;
}

.item__btn {
  padding: 0;
  border: none;
  background-color: transparent;
}

.item__btn:hover {
  cursor: pointer;
}

.item__btn:hover > .item > .item__image {
  outline: 3px solid #ffe495;
}

.item__btn:hover > .item > .item__price {
  color: #ffe495;
}

.item__stats {
  visibility: hidden;
  position: fixed;
}
