.evolution {
  margin: 0 auto;
  padding: 0;
  width: 345px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  list-style-type: none;
}

.subitemTree {
  margin: 0 auto;
  padding: 0;
  display: flex;
  list-style-type: none;
  gap: 10px;
}

.subitemTree > li {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

.item {
  width: 70px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0px;
  outline: 1px solid white;
}

.item__image {
  display: block;
  max-width: 100%;
  outline: 1px solid #f5b35d;
}

.item__price {
  margin: 8px auto;
  color: #eeeff3;
  font-size: large;
}

.item__link:hover>.item>.item__image {
  outline: 3px solid #ffe495;
}

.item__link:hover>.item>.item__price {
  color: #ffe495;
}

.item_disabled {
  display: none;
}

.item__btn {
  padding: 0;
  border: none;
  background-color: transparent;
}

.item__btn:hover {
  cursor: pointer;
}

.item__btn:hover>.item>.item__image {
  outline: 3px solid #ffe495;
}

.item__btn:hover>.item>.item__price {
  color: #ffe495;
}

.item__reqs {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.item__description {
  width: 100%;
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.item__dispname {
  font-size: 20px;
  text-align: center;
}

.item__type {
  color: #ffe396;
  font-size: 18px;
}

.item__effect {
  font-size: 16px;
}

.stat {
  display: flex;
  align-items: center;
}

.stat__text {
  font-size: 16px;
}

.stat__image {
  width: 32px;
}
