.page {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 30px 0;
}

/* upper roles filter panel */
.roles {
  display: flex;
  gap: 20px;
  padding: 0 16px 16px;
  margin: 0 auto;
}

.role__btn {
  mask-image: url('../../images/roles/role-all.png');
  -webkit-mask-image: url('../../images/roles/role-all.png');
  mask-size: contain;
  -webkit-mask-size: contain;
  mask-repeat: no-repeat;
  -webkit-mask-repeat: no-repeat;
  mask-position: center;
  -webkit-mask-position: center;
  border: none;
  width: 40px;
  height: 40px;
  opacity: .5;
}

.role__btn:hover {
  cursor: pointer;
  opacity: 1;
}

.role__btn_active {
  background-color: #f8e8a6;
  opacity: 1;
}

.role__btn_active:hover {
  cursor: default;
}

.role__btn_offlane {
  mask-image: url('../../images/roles/role-offlane.png');
  -webkit-mask-image: url('../../images/roles/role-offlane.png');
}

.role__btn_jungle {
  mask-image: url('../../images/roles/role-jungle.png');
  -webkit-mask-image: url('../../images/roles/role-jungle.png');
}

.role__btn_mage {
  mask-image: url('../../images/roles/role-mage.png');
  -webkit-mask-image: url('../../images/roles/role-mage.png');
}

.role__btn_carry {
  mask-image: url('../../images/roles/role-carry.png');
  -webkit-mask-image: url('../../images/roles/role-carry.png');
}

.role__btn_support {
  mask-image: url('../../images/roles/role-support.png');
  -webkit-mask-image: url('../../images/roles/role-support.png');
}

/* Heroes section */
.heroes {
  width: 100%;
}

.heroes__list {
  list-style-type: none;
  padding: 8px;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: flex-start;
  gap: 15px 20px;
  height: 700px;
  overflow-y: scroll;
  max-height: 700px;
}

.heroes__list::-webkit-scrollbar {
  width: 8px;
  background: #333333;
}

.heroes__list::-webkit-scrollbar-thumb {
  background: #ce9a1b;
}

/* Hero card */
.hero {
  width: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0px;
}

.hero_disabled {
  display: none;
}

.hero__image {
  display: block;
  max-width: 100px;
  outline: 1px solid #f5b35d;
}

.hero__name {
  margin: 12px auto 16px;
  color: #eeeff3;
}

.hero__link:hover > .hero > .hero__image {
  outline: 3px solid #ffe495;
}

.hero__link:hover > .hero > .hero__name {
  color: #ffe495;
}
