.page {
  display: flex;
  flex-direction: column;
  max-width: 1024px;
  width: 100%;
  gap: 20px;
  margin: 0 auto;
}

.header {
  display: flex;
  flex-direction: row;
  gap: 10px;
  text-align: center;
  vertical-align: middle;
}

.header__matchId {
  margin: auto 0;
}
.header__matchId:hover {
  text-decoration: underline;
  cursor: pointer;
}

.header__matchType {
  padding: 4px;
  background-color: #af89af;
  border-radius: 6px;
}

.header__matchRegion {
  padding: 4px;
  background-color: #69afaf;
  border-radius: 6px;
}

.timestamps {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 10px;
  width: 75%;
  margin: 0 auto;
}

.matchData {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.teamsBlock {
  display: flex;
  flex-direction: row;
  gap: 15px;
  width: 100%;
  margin: 0 auto;
}

.teamsBlock__team {
  display: flex;
  flex-direction: column;
  gap: 15px;
  width: 50%;
  margin: 0;
  padding: 0;
}
